<template lang="pug">
.page-car-form
    my-nav-bar(title="询最低价")
    .title 预购车辆
    car-list(:count="1" type="row")
    .title 详细信息
    van-cell-group.form
        van-field(label="姓名" icon="question" placeholder="请输入用户名")
        van-field(label="电话" icon="question" placeholder="请输入用户名")
    .tips 以上信息仅I品车工作人员可见，请放心填写
    my-bottom-bar.bottom
        .btn 确认
</template>

<script>
import CarList from '@/components/CarList';
export default {
    components: {
        'car-list': CarList
    }
};
</script>

<style lang="scss" scoped>
.page-car-form {
    .title {
        padding: 1rem;
    }
    .tips {
        color: red;
        font-size: 0.87rem;
        text-align: center;
        padding: 1rem 0;
    }
    .btn {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;
    }
}
</style>



